body {
  margin: 0;
  font-family: sans-serif;
}

.hidden {
  display: none;
}
.App {
  display: flex;
  flex-direction: column;
  align-content: center;
  min-height: 100vh;
}

.ButtonGroup {
  display: flex;
  
}

.Button {
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  display: inline-block;
  padding: .8em 1.5em;
  border-radius: 3px;
  height: 56px;
  font-size: 14px;
  color: white;
  background-color: #29B9AD;
  outline: none;
  border: none;
}
.Button:hover {
  background-color: #00DBCD;
}
.Button.small {
  height: 20px;
  padding: .2em .8em;
  font-size: 12px;
}

.TextArea {
  resize: none;
  outline: none;
  border: 2px solid #D4D4D4;
  border-radius: 6px;
  padding: .5em;
}

.TextArea::-webkit-input-placeholder {
  color: grey;
  font-size: 18px;
}

.TextArea::-ms-input-placeholder {
  color: grey;
  font-size: 18px;
}

.TextArea::placeholder {
  color: grey;
  font-size: 18px;
}
.Header {
  display: flex;
  flex-wrap: wrap;
  position: fixed;
  min-height: 125px;
  width: 100%;
  align-items: center;
  justify-content: center;
  background-color: #0C193A;
  z-index: 3;
}

.Header .title {
  color: white;
  margin-left: 14px;
  font-size: 32px;
  font-family: sans-serif;
}

@media screen and (max-width: 768px) {
  .Header {
    position: absolute;
  }
  .Header .title {
    font-size: 32px;
  }
}
.Page {
  flex: 1 1;
  padding: 150px 24px 0px 24px;
}
.EditArea {
  display: flex;
  flex-direction: column;
}

.EditArea .HighlightArea + .Button {
  margin-top: 300px;
}
.ResultArea {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
}

.ResultArea .Card {
  padding: 12px;
  margin: 3px;
  cursor: pointer;
  border: 2px solid #D4D4D4;
  border-radius: 4px;
}
.ResultArea .Card.selected,
.ResultArea .Card:hover {
  border-color: #29B9AD;
  background-color: #00DBCD;
}

.ResultArea .Card .key {
  margin-right: 6px;
  font-weight: bold;
}
.HighlightArea {
  box-sizing: border-box;
  width: 100%;
  position: relative;
}

.HighlightArea .Highlight-backdrop {
  box-sizing: border-box;
  width: 100%;
  height: 300px;
  overflow: auto;
  background-color: white;
  position: absolute;
  pointer-events: none;
  padding: 10px;
  border: 2px solid #D4D4D4;
}

.HighlightArea .Highlights {
  white-space: pre-wrap;
  word-wrap: break-word;
  color: transparent;
  font-size: 14px;
  font-family: sans-serif;
  letter-spacing: 1px;
}
.HighlightArea .Highlights mark {
  border-radius: 3px;
  color: transparent;
  background-color: #29B9AD;
  padding: 0px 2px 4px 4px;
  position: relative;
  right: 3px;
  bottom: 1px;
}

.HighlightArea textarea {
  box-sizing: border-box;
  width: 100%;
  height: 300px;
  padding: 10px;
  background-color: transparent;
  color: black;
  position: absolute;
  z-index: 1;
  resize: none;
  font-size: 14px;
  font-family: sans-serif;
  letter-spacing: 1px;
}
.Checkbox label {
  margin: auto;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
  cursor: pointer;
}
.Checkbox label span {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.Checkbox label span:first-child {
  position: relative;
  width: 18px;
  height: 18px;
  border-radius: 3px;
  -webkit-transform: scale(1);
          transform: scale(1);
  vertical-align: middle;
  border: 1px solid #9098A9;
  transition: all .2s ease;
}
.Checkbox label span:first-child svg {
  position: absolute;
  top: 3px;
  left: 2px;
  fill: none;
  stroke: white;
  stroke-width: 3;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-dasharray: 16px;
  stroke-dashoffset: 16px;
  transition: all .3s ease;
  transition-delay: .1s;
  -webkit-transform: translate3d(0,0,0);
          transform: translate3d(0,0,0);
}
.Checkbox label span:first-child::before {
  content: "";
  width: 100%;
  height: 100%;
  background-color: #29B9AD;
  display: block;
  -webkit-transform: scale(0);
          transform: scale(0);
  opacity: 1;
  border-radius: 50%;
}
.Checkbox label span:last-child {
  padding-left: 8px;
}
.Checkbox label:hover span:first-child {
  border-color: #29B9AD;
}
.Checkbox input:checked + label span:first-child {
  background: #29B9AD;
  border-color: #29B9AD;
}
.Checkbox input:checked + label span:first-child svg {
  stroke-dashoffset: 0;
}
.Checkbox:hover label span:first-child {
  background-color: #00DBCD !important;
  border-color: #00DBCD !important;
}
.Footer {
  background-color: #0C193A;
  min-height: 80px;
  color: white;
  box-sizing: border-box;
  padding: 24px;
  display: flex;
  flex-wrap: wrap;
  z-index: 1;
}

.Footer .IconText {
  display: flex;
  align-items: center;
  margin-right: 36px;
  cursor: pointer;
}
.Footer .IconText img {
  height: 24px;
  width: 24px;
  margin-right: 6px;
}

.Footer .personal-info {
  display: flex;
  flex-direction: column;
  margin-right: 100px;
}

@media screen and (min-width: 330px) and (max-width: 768px) {
  .Footer .personal-info {
    flex-direction: row;
    justify-content: space-between;
    width: 100%;
    margin-right: 0px;
    margin-bottom: 20px; 
  }

  .Footer .IconText {
    margin-bottom: 10px;
  }
}

@media screen and (max-width: 330px) {
  .Footer .personal-info {
    margin-bottom: 10px;
  }
  .Footer .personal-info span {
    margin-bottom: 5px;
  }

  .Footer .IconText {
    margin-bottom: 5px;
  }
}
